body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
button.icon {
  background: none;
  outline: none;
  border: none;
  cursor: pointer;
}

/* Post */
.article {
  margin: 2rem 0;
}
.post-wrapper {
  width: 100%;
}

/* Blogs */
.blogs {
  margin: 2rem 0;
}
 /* About me */
 .about {
  margin: 2rem 0;
 }
/* Code */
.code {
  position: relative;
}
.code .copy-icon {
  position: absolute;
  top: 1rem;
  right: 1rem;
  z-index: 5;
}

/* Header */
.navbar-nav .nav-item .nav-link {
  position: relative;
  transition: all 0.3s;
}

.navbar-nav .nav-item .nav-link::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: transparent;
  transition: all 0.3s;
}

.navbar-nav .nav-item .nav-link:hover {
  text-decoration: none;
}

.navbar-nav .nav-item .nav-link:hover::after {
  background-color: rgb(123, 78, 213);
}


/* grid */
.project-grid {
  list-style: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-flow: dense;
  column-gap: 1rem;
  margin: auto;
}

.project-wrapper {
  list-style-type: none;
  flex-basis: 25%;
  margin-bottom: 0px;
  padding: 1rem;
  width: 100%;
}

.project-wrapper,
.project-description {
width: 95%;
}

.project-wrapper img {
  width: 100%;
  margin-bottom: 1em;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.25);
}

@media (max-width: 1250px) {
   
  
  .project-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 750px) {
   
  
  .project-grid {
    grid-template-columns: repeat(1, 1fr);
  }
}